import React, {Component} from "react";
import CasesPerDate from "../components/CasesPerDate";

class HomePage extends Component{

    constructor(props) {
        super(props);
        this.state={
            isLoading:true,
            deathInWorld:0,
            casesInWorld:0,
            healedInWorld:0,
            lastUpdate:null
        };
    }

    componentDidMount() {

        if(this.props.items){

            let {items} = this.props;

            this.setState({
                deathInWorld:items.deaths,
                casesInWorld:items.confirmed,
                healedInWorld:items.recovered,
                lastUpdate: new Date(items.last_update),
                isLoading:false
            });

        }


    }

    render() {
            let {isLoading,lastUpdate}=this.state;
            let formattedDate=null;
            if(lastUpdate instanceof Date){
                //getMonth returns value from 0
                let month=lastUpdate.getMonth()+1;
                formattedDate= lastUpdate.getDate()+'/'+ month +'/'+ lastUpdate.getFullYear();
            }
            let data= !isLoading ? (
                <div>


                    <div className="container">
                        <div>
                            <p>Total Coronavirus cases in the world : {new Intl.NumberFormat('it-IT').format(this.state.casesInWorld)}</p>
                        </div>
                        <div>
                            <p>Total deaths in the world : {new Intl.NumberFormat('it-IT').format(this.state.deathInWorld)}</p>
                        </div>
                        <div>
                            <p>Total healed in the world : {new Intl.NumberFormat('it-IT').format(this.state.healedInWorld)}</p>
                        </div>
                        <div>
                            <p> Last update info : {  formattedDate }</p>
                        </div>

                    </div>
                    <div className="flex flex-center">
                        <CasesPerDate dates={this.props.itemsPerDate}></CasesPerDate>
                    </div>
                </div>
            ) : null;
            return (
                <div className="home">
                    <div className="globe">
                        <i className="fa fa-globe fa-10x" style={{color: '#fff'}}></i>
                    </div>
                    {data}
                </div>
            )
    }
}

export default HomePage;
