import React, {Component} from "react";
import AccordionComponent from "./AccordionComponent";

class CasesPerDate extends Component{
    constructor(props) {
        super(props);
        this.state={
            data:null
        };
    }

    componentDidMount() {

        this.setState({
            data: this.props.dates
        });
    }

    render() {

        let {data}= this.state;
        let dates=null;
        if(data){

            dates=data.map((date)=>{

                console.log(date);
                return <AccordionComponent maintext={date.t} deaths={date.d} recovered={date.r} cases={date.c} ></AccordionComponent>
            });

        }

        return (
            <div>
                {dates}
            </div>
        );
    }

}


export default CasesPerDate;
