import React, {Component} from "react";
import LoaderComponent from "../components/LoaderComponent";
import HomePage from "./HomePage";
import Axios from "axios";
import {Link} from "react-router-dom";

export default class Index extends Component{

    constructor(props) {
        super(props);
        this.state={
            isLoading:true,
            datas:null,
            dataPerDate:null
        };
    }

    componentDidMount() {

        Axios.get(`https://enrichman.github.io/covid19/world/full.json`)
            .then(res => {
                const datas = res.data;
                this.setState({
                    datas,
                    dataPerDate: datas.ts.reverse()
                });
            }).then(()=>this.setState({
            isLoading:false
        }));



    }

    render() {
        let {datas,dataPerDate}= this.state;
        let view = this.state.isLoading ? (
            <LoaderComponent></LoaderComponent>
        ) : (
            <div>
                <header className="App-header">
                    <p>Covid Site</p>
                    <div className="header-right">
                        <p>
                            <Link to="/nations" className="link">Nations</Link>
                        </p>
                    </div>
                </header>
                <HomePage  items={datas} itemsPerDate={dataPerDate}></HomePage>
            </div>
        );
        return (

            <div className="App">
                {view}
            </div>
        );



    }


}
